@include tablet
{
    .seccion-inferior
    {
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;

        h2
        {
            font-size: 3.5rem;
            font-weight: bold;
            margin: 3rem 0;
        }
    }
}

.entrada-blog
{
    margin-bottom: 2rem;

    @include tablet
    {
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }

    &:last-of-type
    {
        margin-bottom: 0;
    }
}

.texto-entrada
{
    a
    {
        color: $negro;
        display: block;
        margin-top: 2rem;

        @include tablet
        {
            margin-top: 0;
        }
    }

    h4
    {
        margin: 0;
        line-height: 1.4;

        &::after
        {
            content: '';
            display: block;
            width: 15rem;
            height: .5rem;
            background-color: $verde;
            margin-top: 1rem;
        }
    }
}

.informacion-meta
{
    span
    {
        color: $amarillo;
    }
}