.no-webp
{
    background-image: url('../img/encuentra.jpg');
}

.webp .imagen-contacto
{
    background-image: url('../img/encuentra.webp');
}

.imagen-contacto
{
    background-position: center center;
    background-size: cover;
    padding: $separacion 0;
    display: flex;
    flex-direction: column;
    align-items: center;

    h2
    {
        font-size: 4rem;
        color: $blanco;
        font-weight: $black;
    }

    p
    {
        font-size: 2.3rem;
        color: $blanco;
        font-weight: 600;
    }
}