@include tablet
{
    .contenedor-anuncios
    {
        @include grid(3, 2rem);
    }
}

.anuncio
{
    background-color: lighten($color: $gris, $amount: 5);
    border: 1px solid darken($color: $gris, $amount: 5);
    
    .contenido-anuncio
    {
        padding: 2rem;
    }

    h3,p 
    {
        margin: 0;
    }

    .anuncio-imagen
    {
        max-height: 20rem;
    }
}

.precio
{
    color: $verde;
    font-weight: bold;
    font-size: 2.4rem;
}

.iconos-caracteristicas
{
    display: flex;
    list-style: none;
    padding: 0;
    max-width: 40rem;

    li
    {
        flex: 1;
        display: flex;

        img
        {
            flex: 0 0 5rem;
            margin-right: 1rem;
        }
    }
}